n-right:50px;margin-top:20px\">导出</el-button>\n        </div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <div style=\"margin:0 20px\">\n        <!-- <el-input placeholder=\"请输入维修厂\"\n                  v-model=\"ListQuery.company\"\n                  class=\"input-with-select search\">\n          <el-button slot=\"append\"\n                     icon=\"el-icon-search\"\n                     @click=\"page=1,init()\"></el-button>\n        </el-input> -->\n        <el-select style=\"margin-right:10px\"\n                   v-model=\"ListQuery.ucp_company\"\n                   placeholder=\"请选择保险公司\"\n                   @change=\"page=1,init()\">\n          <el-option v-for=\"(item,index) in companyList\"\n                     :key=\"index\"\n                     :label=\"item.company\"\n                     :value=\"item.id\"></el-option>\n        </el-select>\n        <!-- <el-date-picker v-model=\"pickerSearch\"\n                        type=\"daterange\"\n                        style=\"width:24%;margin-right:10px\"\n                        @change=\"page=1,init()\"\n                        value-format=\"yyyy-MM-dd\"\n                        range-separator=\"至\"\n                        start-placeholder=\"开始日期\"\n                        end-placeholder=\"结束日期\">\n        </el-date-picker> -->\n        省:\n        <el-select v-model=\"ListQuery.search['province']\"\n                   placeholder=\"请选择省\"\n                   class=\"region\"\n                   @change=\"getCity\">\n          <el-option v-for=\"item in provincelist\"\n                     :key=\"item.id\"\n                     :label=\"item.name\"\n                     :value=\"item.id\">\n          </el-option>\n        </el-select>\n        市:\n        <el-select v-model=\"ListQuery.search['city']\"\n                   placeholder=\"请选择市\"\n                   class=\"region\"\n                   @change=\"getCounty\">\n          <el-option v-for=\"item in citylist\"\n                     :key=\"item.id\"\n                     :label=\"item.name\"\n                     :value=\"item.id\">\n          </el-option>\n        </el-select>\n        区/县:\n        <el-select v-model=\"ListQuery.county\"\n                   placeholder=\"请选择区/县\"\n                   @change=\"changeCounty\"\n                   class=\"region\">\n          <el-option v-for=\"item in countyList\"\n                     :key=\"item.id\"\n                     :label=\"item.name\"\n                     :value=\"item.id\">\n          </el-option>\n        </el-select>\n        <!-- <el-button type=\"primary\"\n                   class=\"printRight\"\n                   @click=\"print\">下载</el-button> -->\n      </div>\n      <el-table :data=\"list\"\n                tooltip-effect=\"dark\">\n        <!-- <el-table-column type=\"selection\"\n                         width=\"55\">\n        </el-table-column> -->\n        <!-- <el-table-column label=\"福利\"\n                         align=\"center\"\n                         prop=\"user_type\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       v-if='scope.row.user_type==1'\n                       @click=\"details(scope.row)\">保险公司</el-button>\n            <span v-else>\n              {{scope.row.user_type==1 ? '保险公司' : scope.row.user_type==2 ? '主机厂' : '其他类型'}}\n            </span>\n          </template>\n        </el-table-column> -->\n        <el-table-column prop=\"ucp_company\"\n                         label=\"保险公司名称\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"company\"\n                         label=\"维修厂名称\"\n                         align=\"center\"></el-table-column>\n        <!-- <el-table-column prop=\"phone\"\n                         label=\"联系电话\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"leader\"\n                         label=\"负责人\"\n                         align=\"center\"></el-table-column> -->\n        <el-table-column label=\"地区\"\n                         align=\"center\">\n\n          <template slot-scope=\"scope\">\n            <el-popover placement=\"top-start\"\n                        title=\"地区详情\"\n                        width=\"200\"\n                        trigger=\"hover\"\n                        :content=\"`${scope.row.province}${scope.row.city}${scope.row.county}`\">\n              <el-button type=\"text\"\n                         slot=\"reference\"\n                         class=\"ellipsis\">{{`${scope.row.province}${scope.row.city}${scope.row.county}`}}</el-button>\n            </el-popover>\n          </template>\n\n        </el-table-column>\n\n        <el-table-column label=\"审核数量\"\n                         align=\"center\"\n                         prop=\"num\">\n        </el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"mini\"\n                       @click=\"details(scope.row)\">详情</el-button>\n\n          </template>\n\n        </el-table-column>\n      </el-table>\n\n      <!-- 分页 -->\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n    <el-dialog title=\"详情\"\n               center\n               :visible.sync=\"detailsVisible\"\n               width=\"80%\"\n               @close='detailsPage=1,detailsCount=1'>\n      <el-table :data=\"detailsList\"\n                class=\"detailsTable\">\n        <el-table-column align=\"center\"\n                         label=\"施工照片\">\n          <template slot-scope=\"scope\">\n            <div>\n              <img v-for=\"(item,index) in scope.row.oil_photo\"\n                   :key=\"index\"\n                   :src=\"item\"\n                   width=\"50px\"\n                   height=\"50px\"\n                   ref=\"oil_photo\" />\n            </div>\n          </template>\n        </el-table-column>\n        <!-- <el-table-column align=\"center\"\n                         prop=\"ucp_company\"\n                         label=\"保险公司名称\">\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"name\"\n                         label=\"车辆名称\">\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"type\"\n                         label=\"车辆类型\">\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"series\"\n                         label=\"车排量\">\n\n        </el-table-column> -->\n        <el-table-column align=\"center\"\n                         prop=\"plate\"\n                         label=\"车牌号\">\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"rankers\"\n                         label=\"类型\">\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"老兵姓名\">\n          <template slot-scope=\"scope\">\n            {{scope.row.ranker==6 ? scope.row.old_name : '无'}}\n          </template>\n        </el-table-column>\n        <!-- <el-table-column align=\"center\"\n                         prop=\"vin\"\n                         label=\"vin码\">\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"policy_num\"\n                         label=\"保单号\">\n          <template slot-scope=\"scope\">\n            {{scope.row.policy_num || '无'}}\n          </template>\n        </el-table-column> -->\n        <el-table-column align=\"center\"\n                         prop=\"\"\n                         label=\"保单图片/优待证\">\n          <template slot-scope=\"scope\">\n\n            <el-button type=\"text\"\n                       v-if=\"scope.row.pc_img && scope.row.pc_img.length>0\"\n                       @click=\"pcImgVisible=true,imgList=scope.row.pc_img,imgDetails()\">详情</el-button>\n            <span v-else>无</span>\n\n          </template>\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"review_time\"\n                         label=\"审核时间\">\n          <template slot-scope=\"scope\">\n            {{scope.row.review_time | datetime}}\n          </template>\n        </el-table-column>\n\n      </el-table>\n\n      <div class=\"page_center\">\n        <paging :page-count=\"detailsCount\"\n                :page=\"detailsPage\"\n                @index=\"detailsPaging\"></paging>\n        <!--分页的组件-->\n      </div>\n      <el-dialog center\n                 title=\"保单图片\"\n                 :visible.sync=\"pcImgVisible\"\n                 append-to-body\n                 width=\"25%\">\n        <div>\n\n          <img v-for=\"(item,index) in imgList\"\n               style=\"width:100px;height:100px;margin-right:20px\"\n               :key='index'\n               :src=\"item\"\n               ref=\"img\" />\n        </div>\n      </el-dialog>\n    </el-dialog>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nimport { getPro } from '@/utils'\nimport region from '@/components/common'\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 1,\n      pageCount: 1,\n      thCurId: '',\n      authList: [],\n      threeAuthList: [],\n      detailsVisible: false,\n      pcImgVisible: false,\n      multipleData: [],\n      detailsList: [],\n      detailsPage: 1,\n      detailsCount: 1,\n      currentDetails: {},\n      ListQuery: {\n        company: '',\n        ucp_company: '',\n        start_time: '',\n        end_time: '',\n        city: '',\n        county: '',\n        search: []\n      },\n      pickerSearch: \"\",\n      provincelist: [],\n      citylist: [],\n      countyList: [],\n      companyList: [], //保险公司列表\n      imgList: []\n    }\n  },\n  created () {\n    this.init();\n  },\n  watch: {\n    pickerSearch (val) {   //获取日期\n      if (val) {\n        this.ListQuery.start_time = val[0]\n        this.ListQuery.end_time = val[1]\n      } else {\n        this.ListQuery.start_time = ''\n        this.ListQuery.end_time = ''\n      }\n    }\n  },\n  methods: {\n    GetCity: region.getCity,\n    GetCounty: region.getCounty,\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    detailsPaging (e) {\n      this.detailsPage = e;\n      this.details(this.currentDetails)\n    },\n    init () {\n      this.$axios.post('admin/UserAudit/channelList', Object.assign(this.ListQuery, { token: this.token, page: this.page, status: 1, pay_status: 1, review: 2 }))\n        .then(res => {\n          this.list = res.data.data.list;\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    //获取保险公司\n    async getCompany () {\n      try {\n        const res = await this.$axios.post('admin/UserAudit/policyCompany', { token: this.token, type_channel: 1 })\n        this.companyList = res.data.data || []\n      } catch (error) {\n        throw (error)\n      }\n    },\n    //获取全部省\n    async getProvince () {\n      try {\n        const res = await getPro();\n        this.provincelist = res;\n      } catch (error) {\n        throw (error)\n      }\n    },\n    //获取市\n    getCity (id) {\n      this.page = 1\n      this.ListQuery.search['province'] = this.query(this.provincelist, id)\n      this.ListQuery.search['city'] = ''  //清空上一次所选的市\n      this.ListQuery.county = ''  //清空上一次所选的区/县\n      this.ListQuery.search['county_id'] = ''  //清空上一次所选的区/县id\n      this.GetCity(id).then(res => {\n        this.citylist = res\n      })\n      this.init()\n    },\n    //获取 区/县\n    getCounty (id) {\n      this.page = 1\n      this.ListQuery.search['city'] = this.query(this.citylist, id)\n      this.ListQuery.county = '' //清空上一次所选的区/县\n      this.ListQuery.search['county_id'] = '' //清空上一次所选的区/县id\n      this.GetCounty(id).then(res => {\n        this.countyList = res\n      })\n      this.init()\n    },\n    changeCounty (id) {\n      this.page = 1\n      this.ListQuery.search['county_id'] = id\n      this.init()\n    },\n    //通过 id 查询\n    query (array, id) {  //array 查询的数组  id 通过当前id查询array数组的数据\n      console.log(array)\n      let newArray = array.filter(item => {\n        return item.id == id\n      })\n      return newArray[0].name\n    },\n    async details (row) {  //详情\n      this.detailsVisible = true\n      this.currentDetails = row\n      try {\n        const res = await this.$axios.post(\"admin/UserAudit/detail\", { token: this.token, sid: row.id, policy_id: row.policy_id, status: row.status, page: this.detailsPage })\n        this.detailsList = res.data.data.list || [];\n        this.detailsCount = res.data.data.rows;\n        this.$nextTick(() => {  //施工图放大\n          if (this.$refs.oil_photo && this.$refs.oil_photo.length > 0) {\n            Viewer(this.$refs.oil_photo)\n          }\n        })\n\n      } catch (error) {\n        throw (error)\n      }\n\n    },\n\n    Export () {  //导出\n      window.location.href = `${this.baseURL}admin/Login/freeMain?ucp_company=${this.ListQuery.ucp_company}&search[\"province\"]=${this.ListQuery.search['province']}&search[\"city\"]=${this.ListQuery.search['city']}&search[\"county_id\"]=${this.ListQuery.county}`\n    },\n    // handleSelectionChange (val) {  //全选/单选\n    //   let tmp = []\n    //   val.forEach(el => {\n    //     tmp.push({ ucp_id: el.ucp_id, uc_id: el.uc_id, user_type: el.user_type })\n    //   });\n\n    //   this.multipleData = tmp\n    // },\n    imgDetails () {  //保单图片详情\n      this.$nextTick(() => {\n        console.log(this.$refs)\n        const img = this.$refs.img\n        Viewer(img)\n      })\n    },\n\n\n    Auth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            var arr = res.data.data;\n            for (var i = 0; i < arr.length; i++) {\n              if (arr[i].son) {\n                if (arr[i].name == '免费保养') {\n                  this.seCurId = arr[i].id;\n                  this.threeAuthList = arr[i].son;\n                }\n                for (var j = 0; j < arr[i].son.length; j++) {\n                  if (arr[i].action != arr[i].son[j].action) {\n                    arr[i].action = arr[i].son[0].action;\n                  }\n                  if (arr[i].son[j].name == '已审核' && arr[i].name == '免费保养') {\n                    this.thCurId = arr[i].son[j].id;\n                  }\n                }\n              }\n            }\n            this.authList = arr;\n          } else {\n            this.$alert(res.data.msg, '提示', { type: 'error' });\n          }\n        })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  mounted () {\n    this.Auth()\n    this.getProvince()\n    this.getCompany()\n\n  },\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 100px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  margin: 0 auto;\n  text-align: center;\n}\n.search {\n  width: 15%;\n  margin-right: 10px;\n}\n.region {\n  width: 10%;\n  margin: 0 10px;\n}\n.img {\n  width: 70px;\n  height: 70px;\n}\n.images {\n  width: 100px;\n  height: 100px;\n  margin-right: 10px;\n  vertical-align: top;\n}\n.laybox2 ul {\n  margin: 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 60px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\n.t-c:after {\n  content: \"\";\n  display: block;\n  height: 0;\n  clear: both;\n}\n\n.bot > div {\n  position: relative;\n}\n\ndiv.bot {\n  margin: 45px auto 35px auto;\n  height: 100px;\n}\n\ndiv.bot span {\n  display: inline-block;\n  width: 20%;\n}\n\ndiv.bot input {\n  display: inline-block;\n  width: 55%;\n  outline: none;\n  border: none;\n  border-bottom: 1px solid #aaa;\n}\n\ndiv.bot button {\n  width: 110px;\n  height: 36px;\n  margin-left: -55px;\n  position: absolute;\n  border-radius: 18px;\n  box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n  border: none;\n  color: #fff;\n  left: 50%;\n  top: 60px;\n}\n\nbutton.reject {\n  background: rgba(237, 81, 55, 1);\n  margin-right: 30%;\n}\n\nbutton.pass {\n  background-color: #20a0ff;\n}\n\n.left {\n  float: left;\n  width: 50%;\n}\n.right {\n  float: right;\n  width: 50%;\n}\n.nowrap {\n  width: 120px !important;\n  text-align: center;\n  white-space: nowrap; /*规定文本不换行**/\n  text-overflow: ellipsis; /**显示省略符号来代表被修剪的文本。*/\n  overflow: hidden;\n  margin: 0 auto;\n}\n.regionDetail >>> .el-dialog__title {\n  font-size: 22px;\n}\n.regionDetail >>> .el-dialog__body {\n  padding: 0px 25px 30px;\n  border-top: solid 2px #eee;\n}\n</style>\n"],"sourceRoot":""}]);

